/*# sourceMappingURL=MobileNav.css.map */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: 375px) {
  .hpBodyDiv {
    position: relative;
  }
  .hpHeaderDiv {
    background: linear-gradient(white, #0ead69, #ffd23f);
    background-position: center;
    background-size: cover;
    height: 500px;
    font-family: "Playfair Display", sans-serif;
  }
  .hpHeaderDiv h1 {
    color: black;
    text-align: center;
    font-size: 40px;
  }
  .hpHeaderDiv h3 {
    text-align: center;
    font-size: 20px;
  }
  .hpHeaderQuestion {
    text-align: center;
  }
  .hpHeaderQuestion h1 {
    font-family: "Nanum Pen Script", cursive;
  }
  .hpHeaderAnswers {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: auto;
        grid-template-columns: auto;
    -ms-grid-rows: auto auto;
        grid-template-rows: auto auto;
  }
  .hpYesAnswerDiv {
    display: -ms-grid;
    display: grid;
    -ms-grid-rows: auto;
        grid-template-rows: auto;
    -ms-grid-columns: auto auto;
        grid-template-columns: auto auto;
  }
  .hpNoAnswerDiv {
    display: -ms-grid;
    display: grid;
    -ms-grid-rows: auto;
        grid-template-rows: auto;
    -ms-grid-columns: auto auto;
        grid-template-columns: auto auto;
  }
}
